<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.body}}</article>
    </div>
</template>

<script>
export default {
  name: 'single-blog',
  data(){
      return{
          id:this.$route.params.id,
          blog:{}
      }
  },
  created(){
      this.$http.get("https://jsonplaceholder.typicode.com/posts/"+ this.id)
    // this.$http.get("./../static/posts/"+this.id)
          .then(function(data){
            //   console.log(data);
              this.blog = data.body;
          })
  }
}
</script>

<style scoped>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 10px 20px;
    background-color: #f9f9f9;
    border:1px dotted #dddddd;
}
article {
    line-height: 150%;
}
</style>



