<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form action="" v-if="!submmited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required>
        <label for="">博客内容</label>
        <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
        <label for="">分类</label>
        <ul id="checkboxes">
            <li v-for="(boxs,index) in blog.checkboxs" :key="index">
                <label>{{boxs}}</label>
                <input type="checkbox" v-bind:value="boxs" v-model="blog.categories">
            </li>
        </ul>
        <label for="">作者</label>
        <select name="" id="" v-model="blog.selected">
            <option v-for="(author,index) in blog.authors" :key="index">{{author}}</option>
        </select>
        <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
        <h3>你的博客发布成功</h3>
    </div>
    <!-- <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>博客分类：{{blog.categories}}</p>
        <p>作者：{{blog.selected}}</p>
    </div> -->
  </div>
</template>

<script>
export default {
    //https://jsonplaceholder.typicode.com/posts
  name: 'add-blog',
  data () {
    return {
      blog:{
          title:"",
          content:"",
          checkboxs:["Vue.js","Node.js","React.js","Angular4"],
          authors:["Hemiah","Henry","Bucky"],
          categories:[],
          selected:[]
      },
      submmited:false
    }
  },
    methods:{
        post:function(){
            this.$http.post("https://jsonplaceholder.typicode.com/posts",{
                title:this.blog.title,
                body:this.blog.content,
                userId:1
            })
            .then(function(data){
                // console.log(data);
                this.submmited = true;
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
    box-sizing: border-box;
}
#add-blog {
    margin: 0px auto;
    max-width: 680px;
    padding: 10px 20px;
    background-color: #f9f9f9;
}
label {
    display: block;
    margin: 20px 2 10px;
    height: 30px;
}
input[type="text"],textarea,select {
    display: block;
    width: 100%;
    padding: 8px;
    margin: 10px 0;
}
#checkboxes {
    margin: 0;
    padding: 0;
}
#checkboxes li { 
    display: inline-block;
    margin: 0 20px 0 0;
}
#checkboxes label {
    display: inline-block;
    margin-top: 0;
}
#checkboxes input {
    display: inline-block;
    margin-right: 10px;
}
button {
    display: block;
    margin: 20px 0;
    background: red;
    color: #ffffff;
    border: 0;
    padding: 14px; 
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px; 
}
h3 {
    margin: 10px 0;
}
</style>



